import React, { useState } from 'react';
import styled from 'styled-components';
import { useMaxBridgeContext } from '@/providers/MaxBridgeProvider.tsx';
import { UserCard } from '@/components/ui/User/UserCard.tsx';
import { MenuItem } from '@/components/ui/Menu/MenuItem.tsx';
import { MenuGrid } from '@/components/ui/Menu/MenuGrid.tsx';
import { SectionTitle } from '@/components/ui/Typography/SectionTitle.tsx';
import { Flex } from '@/components/ui/StyledComponents.tsx';

// Types
interface MenuItemType {
  name: string;
  description: string;
  icon: string;
  path: string;
}
export const PageContainer = styled(Flex)`
  flex: 1;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
`;

export const PageContent = styled(Flex).attrs({
  $direction: 'column',
  $justifyContent: 'flex-start',
})`
  flex: 1;
  height: 100%;
  box-sizing: border-box;
  padding: ${props => props.theme.spacing.md};
  background: ${props => props.theme.colors.white};
  border-radius: ${props => props.theme.borderRadius.large};
  box-shadow: ${props => props.theme.shadows.small};
`;

export const Home: React.FC = () => {
  const { user, hapticFeedback } = useMaxBridgeContext();
  const [showDevTools, setShowDevTools] = useState(false);

  //   
  const mainMenuItems: MenuItemType[] = [
    {
      name: ' ',
      description: '',
      icon: '',
      path: '/booking',
    },
    {
      name: '    ',
      description: '',
      icon: '',
      path: '/doctor-appointment-make',
    },
  ];

  //    
  const devMenuItems: MenuItemType[] = [
    {
      name: 'DEMO AUTH',
      description: 'AUTH',
      icon: '',
      path: '/auth',
    },
    {
      name: 'DEBUG',
      description: 'DEBUG',
      icon: '',
      path: '/debug',
    },
    {
      name: 'maxDemo',
      description: 'maxDemo',
      icon: '',
      path: '/maxDemo',
    },
  ];

  const handleMenuClick = () => {
    hapticFeedback('impact', 'light');
  };

  const toggleDevTools = () => {
    hapticFeedback('impact', 'light');
    setShowDevTools(!showDevTools);
  };

  return (
    <PageContainer className="page-container">
      <PageContent className="page-content">
        {/*   */}
        {user && <UserCard user={user} />}

        {/*   */}
        <MenuGrid>
          {mainMenuItems.map(item => (
            <MenuItem
              key={item.path}
              name={item.name}
              description={item.description}
              icon={item.icon}
              path={item.path}
              onClick={handleMenuClick}
            />
          ))}
        </MenuGrid>

        <SectionTitle onClick={toggleDevTools}> </SectionTitle>
        {showDevTools && (
          <MenuGrid>
            {devMenuItems.map(item => (
              <MenuItem
                key={item.path}
                name={item.name}
                description={item.description}
                icon={item.icon}
                path={item.path}
                onClick={handleMenuClick}
              />
            ))}
          </MenuGrid>
        )}
      </PageContent>
    </PageContainer>
  );
};
